import { getCss, color as colorUtils } from '../../utils';

export const createStyles = (
  direction: 'left' | 'right',
  zIndex: number,
  fixedWidth: boolean
): string => {
  return getCss({
    '@global': {
      ':host': {
        display: 'inline-block',
        position: 'relative',
      },
      ul: {
        listStyle: 'none',
        margin: '0',
        padding: '0',
        maxWidth: fixedWidth ? '176px' : 'auto',
      },
      '.dc-ui-context__trigger': {
        position: 'relative',

        '&:before': {
          content: '""',
          position: 'absolute',
          zIndex: 3,
          bottom: '0',
          left: '0',
          right: '0',
          height: '4px',
          backgroundColor: colorUtils.white,
          display: 'none',
        },

        '&--open': {
          boxShadow: '0 2px 6px 0 rgba(0,0,0,0.3)',

          '&:before': {
            display: 'block',
          },
        },
      },
      '.dc-ui-context__content': {
        display: 'none',
        position: 'absolute',
        flexDirection: 'column',
        backgroundColor: colorUtils.white,
        boxShadow: '0 2px 6px 0 rgba(0,0,0,0.3)',
        top: '100%',
        width: 'max-content',
        minWidth: '100%',
        zIndex: zIndex,
        [direction]: '0',

        '&--open': {
          display: 'inline-block',
        },
      },
    },
  });
};
